
/* center the fixed-size app */
.app-wrapper {
  width: 100vw;
  height: 100vh;
  background-color: #071a0f; 
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

/* fixed 720p area */
.app-fixed {
  width: 1280px;
  height: 720px;
  overflow: hidden;
  position: relative;
  background-color: #071a0f; 
}

html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #071a0f;
}

.row-block {
  margin-bottom: 2rem;
}

.row-title {
  font-size: 1.1rem;
  color: #ffffff;
  margin-bottom: 0.5rem;
  padding-left: 10px;
  font-weight: 400;
}

.row-container {
  display: flex;
  gap: 16px;
  padding: 1rem 0;
  overflow-x: hidden; 
  padding-left: 10px;
  padding-right: 10px;
  scroll-padding-left: 10px;
}

.row-fade {
  animation: fadeIn 0.4s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.tile {
  width: 240px;
  height: 135px;
  flex-shrink: 0;           
  background-size: cover;
  background-position: center;
  border-radius: 2px;
  transition: transform 0.3s ease;
}

.tile-focused {
  transform: scale(1.05);
  border: 2px solid white;
}

.tile-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 2px;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: #111111;
  padding: 1.5rem;
  border-radius: 8px;
  max-width: 800px;
  width: 90%;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  position: relative;
}

.modal-video {
  width: 100%;
  border-radius: 6px;
}

.modal-info {
  display: flex;
  gap: 2rem;
  justify-content: space-between;
  font-size: 0.95rem;
  flex-wrap: wrap;
}

.modal-info-left,
.modal-info-right {
  flex: 1;
  min-width: 200px;
}
